import axios from "axios";
import { React, useState } from "react";
import "./movie.css";
export default function Movie() {
  const [search, setSearch] = useState("");
  const [data, setData] = useState("");
  const submit = () => {
    axios
      .get(`https://www.omdbapi.com/?t=${search}&apikey=16ba5256`)
      .then((res) => setData(res.data));
  };
  return (
    <div className="movie">
      <h1 align="center">Movie Ratings based on IMDb</h1>
      <input
        type="text"
        placeholder="Movie Name"
        autoCorrect="on"
        onChange={(e) => setSearch(e.target.value)}
        name="search"
        required
        autoComplete="on"
        autoFocus="on"
        autoCapitalize="on"
      />
      <i
        className="icon fas fa-info-circle"
        title="Please give correct movie name."
      ></i>
      <button
        id="b1"
        onClick={() => {
          submit();
        }}
      >
        Fetch Movie Data
      </button>

      {data && (
        <table>
          <thead>
            <h2 align="center">Movie Data</h2>
          </thead>
          <tbody>
            <tr>
              <td>Movie Name</td>
              <td>{data.Title}</td>
            </tr>
            <tr>
              <td>Released date</td>
              <td>{data.Released}</td>
            </tr>
            <tr>
              <td>Genre</td>
              <td>{data.Genre}</td>
            </tr>
            <tr>
              <td>Actors</td>
              <td>{data.Actors}</td>
            </tr>
            <tr>
              <td>Director</td>
              <td>{data.Director}</td>
            </tr>
            <tr>
              <td>Short Note</td>
              <td>{data.Plot}</td>
            </tr>
            <tr>
              <td>Poster</td>
              <td>
                <img src={data.Poster} alt={data.Title} />
              </td>
            </tr>
            <tr>
              <td>IMDb Rating</td>
              <td>{data.imdbRating}</td>
            </tr>
          </tbody>
        </table>
      )}
    </div>
  );
}
